const axios = require('axios')
module.exports = {
    random(min, max) {
        let random = Math.floor(Math.random() * (max - min) + 1) + min
        return random
    },
    toNewObj(Obj) {
        let newObj = Object.assign({}, Obj)
        return newObj
    },
    async jasonJump(send_url, method, headers, body) {
        let requestCheck = send_url && method && headers && body
        let Respone = {}
        if (requestCheck) {
            let Request = {}
            let option = {}

            let url = send_url
            option = {
                method,
                headers,
                body
            }
            Request = { url, option }
            Respone = (await axios.post(`http://13.114.170.124/fetch`,Request)).data
        }else{
            Respone['returnCode'] ='2001'
            Respone['returnMessage'] = '資料遺漏'
        }
        return Respone
    }
}