import Api from './Api'

export default {
    // 獲得所有資料
    allData() {
        return Api().get(`/api/media/data`)
    },

    // 修改上半部
    putTOP(infos){
        return Api().put(`/api/media/message`,infos)
    },

    // 新增圖片
    updateIMG(img){
        return Api().post(`/api/media/carousel`,img)
    },

    // 刪除圖片
    deleteIMG(index){
        return Api().delete(`/api/media/carousel/${index}`)
    }
}