<template>
  <div class="text-center" style="min-height:80vh">
    <!-- APP Bar -->
    <v-app-bar color="main" height="60px" elevate-on-scroll app>
      <div class="d-flex justify-center align-center" style="width:100vw;">
        <v-btn
          v-if="0"
          icon
          class="white--text"
          style="position: absolute; left: 20px;"
          small
          @click="goBack"
        >
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        <span class="main_title white--text">多媒體機管理系統</span>
      </div>
    </v-app-bar>
    <v-progress-linear v-if="loading" indeterminate color="success" />
    <!-- Main -->
    <div v-if="!loading">
      <div class="d-flex align-center flex-column mt-3">
        <p class="font-weight-bold" style="font-size:1.8em">多媒體機管理系統</p>
        <div style="width:80vw">
          <v-form ref="top">
            <v-container class="d-flex flex-column" fluid>
              <v-row>
                <v-col cols="4">
                  <v-text-field
                    v-model="updateTOP_data.parkName"
                    :rules="[rules.require]"
                    outlined
                    label="場站名稱"
                    color="main"
                    dense
                  ></v-text-field>
                </v-col>
                <v-col cols="4">
                  <v-text-field
                    v-model="updateTOP_data.welcome"
                    :rules="[rules.require]"
                    outlined
                    :counter="7"
                    label="迎賓訊息"
                    color="main"
                    dense
                    clearable
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row class="mt-n4">
                <v-col cols="8">
                  <v-text-field
                    v-model="updateTOP_data.code"
                    :rules="[rules.require]"
                    outlined
                    :counter="36"
                    label="費率提示"
                    color="main"
                    dense
                    clearable
                  ></v-text-field>
                </v-col>
                <v-col cols="4">
                  <v-btn
                    depressed
                    :loading="changeLoading || loading"
                    class="main white--text"
                    @click="updateTOP()"
                  >更新</v-btn>
                  <v-btn
                    outlined
                    :disabled="changeLoading || loading"
                    class="ml-3 main--text"
                    @click="cancelData()"
                  >取消</v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </div>
      </div>
      <div class="text-right">
        <v-btn
          class="main white--text mr-10"
          @click="updateAdImg=null,adImg = null,dialogs.ad = true"
        >新增圖片</v-btn>
      </div>
      <v-container style="padding:1% 3%;" fluid>
        <v-row>
          <v-col cols="4" v-for="(carousel,i) in carousel" :key="i">
            <v-card outlined width="72vh">
              <v-btn
                icon
                small
                class="error white--text"
                style="z-index:1; position: absolute; right:-3%; top:-8%"
                @click="dialogs.delete=true,deleteIndex=i"
              >
                <v-icon size="1.6em">mdi-close</v-icon>
              </v-btn>
              <v-img :src="carousel" width="72vh" aspect-ratio="2.4"></v-img>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </div>

    <!-- Dialog -->
    <div>
      <v-dialog v-model="dialogs.ad" width="80vh" :persistent="IMGloading">
        <v-card max-height="90vh" style="overflow-y:auto;">
          <v-container>
            <p class="text-center main--text" style="font-size:1.5em">新增圖片</p>
            <!-- 無公告 -->
            <div v-if="!adImg" class="d-flex justify-center">
              <v-sheet
                class="d-flex mx-4"
                color="grey lighten-2"
                width="72vh"
                height="30vh"
                elevation="2"
              >
                <v-progress-linear v-if="IMGloading" indeterminate color="main"></v-progress-linear>
              </v-sheet>
            </div>
            <!-- 有公告 -->
            <v-card v-if="adImg" class="d-flex justify-center mx-4">
              <v-img :src="adImg" width="72vh" aspect-ratio="2.4">
                <v-progress-linear v-if="IMGloading" indeterminate color="main"></v-progress-linear>
              </v-img>
            </v-card>
            <p
              class="success lighten-5 text-center mt-2 mx-4"
              style="font-size:0.8em"
            >#圖片建議大小: 900×2160 (比例 5:12)</p>

            <v-divider></v-divider>
            <div class="px-8 mt-3" ref="ad">
              <v-row>
                <v-file-input
                  v-model="updateAdImg"
                  label="上傳圖片"
                  filled
                  prepend-icon="mdi-image"
                  accept=".jpg, .jpeg"
                  :loading="IMGloading"
                  color="main"
                  hint="檔案格式要求: .jpg .jpeg"
                  persistent-hint
                  @change="updateIMG()"
                >
                  <template v-slot:selection="{ text }">
                    <v-chip small label class="main white--text">{{ text }}</v-chip>
                  </template>
                </v-file-input>
              </v-row>
            </div>
          </v-container>
        </v-card>
      </v-dialog>
      <v-dialog v-model="dialogs.delete" width="60vh" :persistent="deleteLoading">
        <v-card max-height="90vh" style="overflow-y:auto;">
          <v-card-title>刪除該廣告</v-card-title>
          <v-card-text>按下確認後，將無法復原。</v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn outlined :disabled="deleteLoading" @click="dialogs.delete=false">取消</v-btn>
            <v-btn
              class="ml-3 main white--text"
              :loading="deleteLoading"
              depressed
              @click="deleteIMG()"
            >確認</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>

    <!-- Snackbar -->
    <div>
      <v-snackbar top right v-model="snackbar.open" color="success" class="black--text">
        <span>{{snackbar.message}}</span>
      </v-snackbar>
    </div>
    <!-- Footer -->
    <div>
      <v-footer class="d-flex justify-center align-center text main" dark app>
        <div
          style="font-size:0.75em"
        >Copyright&copy; {{ new Date().getFullYear() }} MitWit Inc. All rights reserved.</div>
      </v-footer>
    </div>
  </div>
</template>
<script>
import { mask } from "vue-the-mask";
import park from "../service/park";
import tools from "../../tools/tools";
export default {
  directives: {
    mask
  },
  mounted() {
    this.allData();
  },
  data() {
    return {
      mask: "####-##-##",
      loading: false,
      dialogs: { ad: false, delete: false },
      adEditType: "add",
      updateTOP_data: { parkName: "", code: "", welcome: "" },
      deleteIndex: "",
      updateAdImg: "null",
      adImg: "",
      deleteLoading: false,
      IMGloading: false,
      changeLoading: false,
      snackbar: { open: false, message: "" },
      infos: { parkName: "", code: "", welcome: "" },
      carousel: [],
      rules: {
        require: value => !!value || "不得為空"
      }
    };
  },
  methods: {
    async allData() {
      this.snackbar.open = false;
      this.loading = true;
      let result = (await park.allData()).data;
      this.infos["parkName"] = result.parkName;
      this.infos["code"] = result.code;
      this.infos["welcome"] = result.welcome;

      this.updateTOP_data = tools.toNewObj(this.infos);

      this.carousel = result.carousel;
      this.loading = false;
    },
    async deleteIMG() {
      this.snackbar.open = false;
      this.deleteLoading = true;
      let result = (await park.deleteIMG(this.deleteIndex)).data;
      if (result.success) {
        this.snackbar.message = "刪除成功";
        this.snackbar.open = true;
        this.dialogs.delete=false
        this.allData();
      } else {
        this.snackbar.message = "刪除失敗";
        this.snackbar.open = true;
      }
      this.deleteLoading = false;
    },
    async updateTOP() {
      if (this.$refs.top.validate()) {
        this.snackbar.open = false;
        this.changeLoading = true;
        let result = (await park.putTOP(this.updateTOP_data)).data;
        if (result.success) {
          this.snackbar.message = "修改成功";
          this.snackbar.open = true;
          this.allData();
        } else {
          this.snackbar.message = "修改失敗";
          this.snackbar.open = true;
        }
        this.changeLoading = false;
      }
    },
    async updateIMG() {
      if (this.updateAdImg) {
        this.snackbar.open = false;
        let formData = new FormData();
        formData.append("adImg", this.updateAdImg);
        this.IMGloading = true;
        let result = (await park.updateIMG(formData)).data;
        if (result.success) {
          this.adImg = result.adImg;
          this.snackbar.message = "照片新增成功";
          this.allData();
          this.snackbar.open = true;
        } else {
          this.snackbar.message = "照片新增失敗";
          this.snackbar.open = true;
        }
        this.IMGloading = false;
      }
    },
    cancelData() {
      this.updateTOP_data = tools.toNewObj(this.infos);
    }
  }
};
</script>
